// 文字常量
import {
  EQUIPMENT_LIVE,
  FORM_CONFIG_DATE,
  MSG,
  CONSTANTS_DEVICE_CONFIGURATION,
  EQUIPMENT_DATA,
  BTN_TEXT,
  ENERGY,
  IP,
  STATUS_CODE,
  FILE_TYPE,
  TITLE,
  REPAIR_TASK
} from '@/utils/constantsFile';
// common service
import { CommonService } from '@/services/common.service';
// 维修任务service
import { FixTaskService } from '@/services/fixTask.service';
// 警告消息
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
import {
  extendPropertyValueInsertOrUpdateBatchAction
} from '@/api/extendConfig';
import {
  delImgAction
} from '@/api/checkConfig';
import {
  selectFile,
  deleteFileById,
  downloadFile,
  fileResourceinsertOrUpdateBatch,
  addOrUpdateRepairTask,
  repairTaskSubmitRepairTaskAction
} from '@/api/equipment';
import { basicInformationGetListByBasicType } from '@/api/management.js';
// import {
//   downloadFile
// } from '@/api/export';
import moment from 'moment';
export default {
  name: 'FixTaskConfirmTaskModal',
  props: {
    title: { type: String, default: '' },
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    confirmLabel: { type: String, default: BTN_TEXT.CONFIRM_FINISH },
    backLabel: { type: String, default: BTN_TEXT.RETURN },
    showDialog: { type: Boolean, default: false },
    // 是否允许上传
    isCanUpload: { type: Boolean, default: false },
    // other
    input: { type: String, default: '' },
    tabs: { type: Array, default: [] },
    active2: { type: Number, default: 0 },
    // requiredData: {
    //   type: Object,
    //   default: () => {
    //     return {
    //       confirmRepairType: null,
    //       endTime: null,
    //       startTime: null,
    //       userIdList: null
    //     };
    //   }
    // },
    dataset: {
      type: Object,
      default() {
        return {
          isGrabMonad: 0,
          isChargeback: 0,
          isRevoke: 0,
          isReview: 0,
          isReject: 0,
          isSave: 0,
          isSubmitReview: 0
        };
      }
    },
    requiredData: { type: Object, default: () => {} },
    showFileHandle: { type: Boolean, default: true },
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    handleClose: Function,
    // 点击显示图片
    handleShowImg: Function,
    publicFun: Function,
    readOnly: { type: Boolean, default: false }
  },
  data() {
    var setValidateForm = (rule, value, callback) => {
      return this.$options.filters.formatterFormValidate(
        rule,
        value,
        callback,
        this.extendPropertyValueVoList
      );
    };
    return {
      setValidateForm: setValidateForm,
      REPAIR_TASK: REPAIR_TASK,
      IP: IP,
      ENERGY: ENERGY,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      MSG: MSG,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      reasonContent: '',
      tableData: [],
      formLabelWidth: '135px',
      showModalFlag: false,
      // 扩展字段
      extendPropertyValueVoList: [],
      uploadConfig: {
        headers: {
          token: window.localStorage.getItem('token').replace(/"/g, '')
        },
        url: IP.PUBLICIP + 'fileResource/uploadOneFile.action',
        data: { // 维修文件
          fileResourceTypeEnum: this.GLOBAL.FILE_TYPE.REPAIR_FILE,
          relationshipId: ''
        },
        dataImg: { // 维修图片
          fileResourceTypeEnum: FILE_TYPE.REPAIR_IMAGE,
          relationshipId: ''
        }
      },
      confirmRules: {
        startTime: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        confirmRepairType: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT }
        ],
        endTime: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        userIdList: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }]
      },
      fileListImg: [],
      delFileList: [],
      taskProcessRecordVoList: [],
      tableLoading: false,
      repairTaskTypeData: [],
      extend18Data: []
    };
  },
  methods: {
    saveBtnshow(item) {
      var flag = false;

      if (this.dataset.isSave === 1) {
        flag = true;
      }
      if (this.active2 === 2 && this.requiredData.repairState === 1) {
        flag = true;
      }
      return flag;
    },
    confirmBtnshow() {
      var flag = false;

      if (this.dataset.isSubmitReview === 1 || this.dataset.isSubmitReview === 0) {
        flag = true;
      }
      if (this.active2 === 2 && this.requiredData.repairState === 1) {
        flag = true;
      }

      return flag;
    },
    datetimeChange(item, e) {
      var _this = this;
      item.extendValue = e;
      _this.$forceUpdate();
    },
    change(e) {
      this.$forceUpdate();
    },
    test(e) {
    },
    /**
     * @description: 初始化modal数据
     * @return: void
     */
    initData: function() {
      this.toExtendRulesAndShow(this.requiredData);
      if (!this.requiredData.startTime) {
        this.requiredData.startTime = this.requiredData.occurTime;
      }
      if (this.requiredData) {
        if (this.requiredData.repairState === 3) {
          this.isCanUpload = true;
        } else {
          this.isCanUpload = false;
        }
        this.uploadConfig.data.relationshipId = this.requiredData.repairTaskId;
        this.uploadConfig.dataImg.relationshipId = this.requiredData.repairTaskId;
        this.taskProcessRecordVoList = this.requiredData.taskProcessRecordVoList;
        this.fileListImg = this.requiredData.repairImageList.map(item => {
          var json = {
            id: item.resourceId,
            url: IP.PUBLICIP + item.fileUrl,
            relationshipId: item.relationshipId,
            flag: true
          };
          return json;
        });
      }
      // 获取下载文件
      if (
        this.showFileHandle &&
        this.requiredData &&
        this.requiredData.repairTaskId
      ) {
        this.getFilesData();
      }
      CommonService.getUsers();
    },
    /**
     * @description: 获取维修任务对应的附件
     * @return: void
     */
    getFilesData() {
      selectFile({
        fileResourceTypeEnum: this.GLOBAL.FILE_TYPE.REPAIR_FILE,
        relationshipId: this.requiredData.repairTaskId
      })
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            this.tableData = FixTaskService.generateFixTaskFileData(res.rows);
          }
        });
    },
    /**
     * @description: 根据url下载文件
     * @param: {String} url 文件url
     * @return: void
     */
    downloadFile(row) {
      downloadFile({
        fileUrl: row.fileUrl
      })
        .then(res => {
          // var arr = url.split('/');
          var filename = row.fileName;
          var blob = new Blob([res]);
          var downloadEle = document.createElement('a');
          var href = window.URL.createObjectURL(blob);
          downloadEle.href = href;
          downloadEle.download = filename;
          downloadEle.click();
          document.body.removeChild(downloadEle);
        });
    },
    /**
     * @description: 处理上传成功事件
     * @param: {Object} response 上传成功后的返回数据
     * @param: {Object} flie 上传的文件
     * @param: {Array} fileList 文件列表
     * @return: void
     */
    handleUploadSuccess(response, flie, fileList) {
      if (response.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
        this.getFilesData();
        this.$refs.upload.uploadFiles = [];
        // for (let index = 0; index < this.tableData.length; index++) {
        //   if (this.tableData[index].uid === flie.uid) {
        //     this.tableData[index].fileUrl = response.rows.fileUrl;
        //     this.tableData[index].resourceId = response.rows.resourceId;
        //   }
        // }
      } else {
        showWarningMessage(response.message);
      }
    },
    /**
     * @description: 删除文件
     * @param: {Int} id 文件id
     * @return: void
     */
    deleteFileById(id) {
      deleteFileById({
        id: id
      })
        .then(res => {
          this.tableData = [];
          this.getFilesData();
        });
    },
    /**
     * @description: 处理点击关闭
     * @return: void
     */
    cancelAction: function() {
      // this.$refs.confirmDataForm.resetFields();
      if (this.handleCancel) {
        var self = this;
        setTimeout(function() {
          self.handleCancel();
        });
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
     */
    // confirmAction: function() {
    //   // if (this.handleConfirm) {
    //   // this.handleConfirm(this.requiredData);
    //   // }
    //   // this.clearData();
    // },
    /**
     * 保存维修数据
     */
    saveRepairData() {
      var data = JSON.parse(JSON.stringify(this.requiredData));
      data.fileList = this.fileList;
      data.startTime = this.requiredData.startTime
        ? moment(this.requiredData.startTime).format('YYYY-MM-DD HH:mm:ss')
        : null;
      data.endTime = moment(new Date().getTime()).format('YYYY-MM-DD HH:mm:ss');
      data.repairUserIdList = this.requiredData.userIdList;
      data.repairUserIds = this.requiredData.userIdList.join(',');

      var param = {
        repairTaskId: data.repairTaskId,
        endTime: data.endTime,
        startTime: data.startTime,
        reasonContent: data.reasonContent ? data.reasonContent : '',
        // repairTypeIds: this.requiredData.repairTypeIdList.join(','),
        repairTypeIdList: this.requiredData.confirmRepairType,
        // repairUserIds: data.repairUserIds
        repairUserIdList: data.repairUserIdList
      };

      if (data.extend18) {
        param.extend18 = data.extend18;
      }
      if (data.repairTaskType) {
        param.repairTaskType = data.repairTaskType;
      }

      addOrUpdateRepairTask(param)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // update 扩展字段
            if (data.extendPropertyValueVoList.length > 0) {
              this.saveExtendValue(data.extendPropertyValueVoList);
            }
            if (this.delFileList.length !== 0) {
              this.delImg();
            }
            setTimeout(() => {
              showSuccessMessage(res.message);
              this.handleConfirm();
              this.clearData();
            }, 300);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * 提交维修确认
     */
    saveConfirmRepairData() {
      var data = JSON.parse(JSON.stringify(this.requiredData));
      data.fileList = this.fileList;
      data.startTime = this.requiredData.startTime
        ? moment(this.requiredData.startTime).format('YYYY-MM-DD HH:mm:ss')
        : null;
      data.endTime = moment(new Date().getTime()).format('YYYY-MM-DD HH:mm:ss');
      data.repairUserIdList = this.requiredData.userIdList;
      data.repairUserIds = this.requiredData.userIdList.join(',');

      var param = {
        repairTaskId: data.repairTaskId,
        endTime: data.endTime,
        startTime: data.startTime,
        reasonContent: data.reasonContent ? data.reasonContent : '',
        // repairTypeIds: this.requiredData.repairTypeIdList.join(','),
        repairTypeIdList: this.requiredData.confirmRepairType,
        // repairUserIds: data.repairUserIds
        repairUserIdList: data.repairUserIdList
      };

      if (data.extend18) {
        param.extend18 = data.extend18;
      }
      if (data.repairTaskType) {
        param.repairTaskType = data.repairTaskType;
      }

      repairTaskSubmitRepairTaskAction(param)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // update 扩展字段
            if (data.extendPropertyValueVoList.length > 0) {
              this.saveExtendValue(data.extendPropertyValueVoList);
            }
            if (this.delFileList.length !== 0) {
              this.delImg();
            }
            setTimeout(() => {
              this.handleConfirm();
              this.clearData();
            }, 300);
            showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 保存扩展字段数据
     * @return: void
     */
    saveExtendValue(data) {
      var arr = data.map((item) => {
        if (item.extendValue === null) {
          item.extendValue = '';
        }
        return item;
      });
      extendPropertyValueInsertOrUpdateBatchAction({}, arr)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // this.getFixTableData();
            // showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
    * @description: 删除图片
    * @param {type}
    * @return:
    */
    async delImg() {
      try {
        const res = await delImgAction({ ids: this.delFileList }, []);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.delFileList = [];
          // 提示正常信息
          // showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 处理点击关闭
     * @return: void
     */
    closeAction: function() {
      if (this.handleClose) {
        this.clearData();
        this.handleClose();
      }
    },
    /**
     * @description: 判断是否能保存
     * @return: void
     */
    checkDataCorrect() {
      this.$refs.confirmDataForm.validate(valid => {
        if (valid) {
          // this.confirmAction();
          this.saveRepairData();
        }
      });
    },
    repairConfirm() {
      this.$refs.confirmDataForm.validate(valid => {
        if (valid) {
          // this.confirmAction();
          this.saveConfirmRepairData();
        }
      });
    },
    /**
     * @description: 上传文件
     * @return: void
     */
    updateFiles: function() {
      for (var i = 0; i < this.$refs.upload.uploadFiles.length; i++) {
        var isExit = false;
        for (var j = 0; j < this.tableData.length; j++) {
          if (this.tableData[j].uid === this.$refs.upload.uploadFiles[i].uid) {
            isExit = true;
          }
        }
        if (!isExit) {
          this.tableData.push(this.$refs.upload.uploadFiles[i]);
        }
      }
    },
    updateFilesImg(file) {
      var types = ['image/JPG', 'image/PNG', 'image/JPEG', 'image/jpeg', 'image/jpg', 'image/png'];
      const isImage = types.includes(file.type);
      if (!isImage) {
        this.$message.error('上传图片只能是 JPG、JPEG、PNG 格式!');
        return false;
      }
      const isLtSize = file.size / 1024 / 1024 < 5;
      if (!isLtSize) {
        this.$message.error('上传图片大小不能超过 5MB!');
        return false;
      }
    },
    /**
     * @description: 清除数据
     * @return: void
     */
    clearData() {
      // this.requiredData = null;
      this.fileListImg = [];
      this.delFileList = [];
    },
    /**
     * @description: 表单中扩展字段处理
     * @return: void
     */
    toExtendRulesAndShow(data) {
      // 保存扩展字段数据
      var arr = data.extendPropertyValueVoList.map(item => {
        item.relationshipTypeId = data.repairTaskId;
        item.extendValue = item.extendValue ? item.extendValue : null;
        return item;
      });
      this.extendPropertyValueVoList = arr;
      var rules = JSON.parse(JSON.stringify(this.confirmRules));
      for (var item in this.extendPropertyValueVoList) {
        // 若类型是日期
        if (this.extendPropertyValueVoList[item].propertyType === 3) {
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: ['blur', 'change'] }
          ];
        } else {
          // 若类型是字符或数字
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: 'blur' }
          ];
        }
      }
      this.confirmRules = rules;
    },
    updateSuccess(response, file, fileList) {
      this.fileListImg.push(file);
      if (response.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
        // this.fileList.push({
        //   id: response.rows.resourceId,
        //   url: IP.PUBLICIP + response.rows.fileUrl,
        //   relationshipId: response.rows.relationshipId
        // });
      }
    },
    showDialgImg(url) {
      this.handleShowImg(url);
    },
    handlePictureCardPreview(file) {
      this.handleShowImg(file.url);
    },
    /**
     * @description: 图片控制
     * @param 无
     * @return:无
     * @author: ZJM
     */
    handleRemove(file, fileList) {
      if (!file.flag) {
        this.delFileList.push(file.response.rows.resourceId);
        this.fileListImg = fileList;
      } else {
        this.delFileList.push(file.id);
        this.fileListImg = fileList;
      }
    },
    saveRepairImg(id, arr) {
      var jsonArr = [];
      for (var i = 0; i < arr.length; i++) {
        var json = {
          relationshipId: id,
          resourceId: arr[i]
        };
        jsonArr.push(json);
      }
      fileResourceinsertOrUpdateBatch({}, jsonArr)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    async getRepairTaskTypeData() {
      try {
        const res = await basicInformationGetListByBasicType({
          basicInformationEnum: 'REPAIR_TASK_TYPE'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          for (var i = 0; i < res.rows.length; i++) {
            arr.push({
              label: res.rows[i].basicValue,
              value: res.rows[i].basicId
            });
          }
          this.repairTaskTypeData = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getExtend18Data() {
      try {
        const res = await basicInformationGetListByBasicType({
          basicInformationEnum: 'REPAIR_TASK_IS_DOWN'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          for (var i = 0; i < res.rows.length; i++) {
            arr.push({
              label: res.rows[i].basicValue,
              value: res.rows[i].basicId
            });
          }
          this.extend18Data = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  watch: {
    requiredData(val, oldVal) {
      if (this.requiredData) {
        this.initData();
      }
      if (this.showDialog) {
        this.showModalFlag = true;
      }
    },
    showDialog(val, oldVal) {
      if (!this.showDialog) {
        this.showModalFlag = false;
      }
    }
  },
  created() {
    this.getRepairTaskTypeData();
    this.getExtend18Data();
    // this.requiredData = {
    //   endTime: null
    // };
    // this.initData();
  }
};
