// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 文字常量
import { EQUIPMENT_DATA, ENERGY, STATUS_CODE, BTN_TEXT, PAGE_TITLE } from '@/utils/constantsFile';
// API
import { deviceLiveGetListTimeParameterRecordByDeviceCode } from '@/api/equipment';
// 引入警告信息
import { showWarningMessage } from '@/utils/message';
// 引入通用方法
// import { CommonService } from '@/services/common.service';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
// 引入table组件
// 搜索框
import SearchBox from '@/components/common/SearchBox';
export default {
  components: {
    HeaderTitle,
    HeadOperationItem,
    SearchBox
  },
  props: {
    selectdata: { type: Object, default: () => {} },
    showBack: { type: Boolean, default: false },
    handleBack: Function
  },
  data() {
    return {
      PAGE_TITLE: PAGE_TITLE,
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      ENERGY: ENERGY,
      // 表格加载标识
      tableLoading: false,
      searchList: [],
      // 快照数据
      relationshipId: null,
      relationshipData: [],
      showFlag: true,
      lineTime: [],
      timeOptionRange: '',
      pickerOptions: {
        // disabledDate(time) {
        //   return time.getTime() > Date.now();
        // },
        onPick: (time) => {
          // 当第一时间选中才设置禁用
          if (time.minDate && !time.maxDate) {
            this.timeOptionRange = time.minDate;
          }
          if (time.maxDate) {
            this.timeOptionRange = null;
          }
        },
        disabledDate: (time) => {
          var timeOptionRange = this.timeOptionRange;
          var secondNum = 60 * 60 * 24 * 7 * 1000;
          if (timeOptionRange) {
            if (timeOptionRange.getTime() + secondNum > Date.now() - 8.64e6) {
              return (
                time.getTime() > Date.now() - 8.64e6 ||
                time.getTime() < timeOptionRange.getTime() - secondNum
              );
            }
            return (
              time.getTime() > timeOptionRange.getTime() + secondNum ||
              time.getTime() < timeOptionRange.getTime() - secondNum
            );
          } else {
            return time.getTime() > Date.now();
          }
        }
      }
    };
  },
  watch: {
    relationshipId(val) {
      console.log(val);
      // if (val.length > 3) {
      //   // showWarningMessage(MSG.ONLY_SELECT_THREE);
      //   // val.pop();
      // } else {
      //   this.showFlag = true;
      //   // this.showEcharts();
      // }
      if (val >= 0) {
        this.showFlag = true;
        this.showEchart(val);
      } else {
        this.showFlag = true;
        this.showEchartsData([], []);
        // val.pop();
      }
    }
  },
  methods: {
    showEchart(val) {
      var nameList = [];
      var valueList = [];
      var series = [];

      var arrJosn = this.relationshipData[val].parameterRecordList;
      for (var i = 0; i < arrJosn.length; i++) {
        nameList.push(arrJosn[i].time);
        valueList.push(Number(arrJosn[i].value));
      }
      series.push({
        name: this.relationshipData[val].parameterName,
        type: 'line',
        data: valueList
      });
      this.showEchartsData(series, nameList);
    },
    showEchartsData(series, nameList) {
      var myChart = this.$echarts.init(
        document.getElementById('showEcharts')
      );

      myChart.setOption({
        title: {
          text: '参数折线图'
        },
        tooltip: {
          trigger: 'axis'
        },
        toolbox: {
          // feature: {
          //   saveAsImage: {}
          // }
        },
        dataZoom: [
          {
            show: true,
            realtime: true,
            start: 0,
            end: 100
          },
          {
            type: 'inside',
            realtime: true,
            start: 0,
            end: 100
          }
        ],
        grid: {
          left: '4%',
          right: '5%',
          bottom: '10%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          name: '时间',
          boundaryGap: false,
          data: nameList
        },
        yAxis: {
          type: 'value',
          name: '参数值'
        },
        series: series
      }, true);
    },
    search() {
      this.showEchartsData([], []);
      // this.showFlag = false;
      this.relationshipData = [];
      this.relationshipId = null;
      this.initData();
      // this.relationshipId = null;
    },
    initData() {
      var params = {
        startTime: this.lineTime[0],
        endTime: this.lineTime[1],
        deviceCode: this.selectdata.deviceCode
      };
      deviceLiveGetListTimeParameterRecordByDeviceCode(params, {}).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.relationshipData = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    goBack() {
      this.handleBack();
    },
    formatDateTime(time) {
      var date = '';
      var D = '';
      if (time !== undefined) {
        date = new Date(time);
      } else {
        date = new Date();
      }
      var y = date.getFullYear();
      var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
      var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
      var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
      var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
      D = y + '-' + M + '-' + d + ' ' + h + ':' + m + ':' + s;

      return D;
    }
  },
  created() {
    this.lineTime = [this.selectdata.createTime];
    if (this.selectdata.endTime) {
      this.lineTime.push(this.selectdata.endTime);
    } else {
      this.lineTime.push(this.formatDateTime(new Date(this.selectdata.createTime).getTime() + 1000 * 60 * 60 * 24));
    }
    this.initData();
  }
};
