/**
 * @Descripttion: 创建维修单JS
 * @version:
 * @Author: Lois
 * @Date: 2020-5-25
 * @LastEditors: Lois
 * @LastEditTime:  2020-6-13
 */
// 文字常量
import { TITLE, EQUIPMENT_LIVE, MSG, CONSTANTS_DEVICE_CONFIGURATION, EQUIPMENT_DATA, BTN_TEXT, ENERGY, PAGE_TITLE, STATUS_CODE, IP, FILE_TYPE, FORM_CONFIG_DATE, REPAIR_TASK } from '@/utils/constantsFile';
// API
import {
  extendPropertyGetListAction,
  extendPropertyValueInsertOrUpdateBatchAction
} from '@/api/extendConfig';
import { selectRepairTypeByDeviceCode, repairTaskinsertRepairTask, fileResourceinsertOrUpdateBatch } from '@/api/equipment';
import { deviceWorkshopTypeGetTreeAction, deviceGroupGetTreeAction, deviceSelectDeviceByConditionAction, basicInformationGetListByBasicType } from '@/api/management.js';
// 导入所需接口
import {
  delImgAction
} from '@/api/checkConfig';
import ZTree from '@/components/common/ZTree/ZTree';
// 引入警告信息
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';

export default {
  components: {
    ZTree
  },
  name: 'CreateNewFixTaskModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.CONFIRM_CREATE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 是否允许上传
    isCanUpload: { type: Boolean, default: false },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function,
    // 点击显示图片
    handleShowImg: Function
  },
  data() {
    var setValidateForm = (rule, value, callback) => {
      return this.$options.filters.formatterFormValidate(
        rule,
        value,
        callback,
        this.extendPropertyValueVoList
      );
    };
    return {
      setValidateForm: setValidateForm,
      REPAIR_TASK: REPAIR_TASK,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      MSG: MSG,
      // 创建的维修单数据
      createData: {},
      // 设备名称加载
      deviceNMloading: false,
      // 维修类型加载
      repairTypeloading: false,
      typeSelect: '1',
      // 选择的设备过滤ids
      selectDeviceFilter: [],
      nodeData: [],
      selectRow: null,
      deviceListTable: {
        page: {
          currentPage: 1,
          pageSize: 10,
          total: 30,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          {
            field: 'deviceName',
            title: ENERGY.DEVICENAME,
            align: 'center'
          }, {
            field: 'devicePriCode',
            title: ENERGY.DEVICECODE,
            align: 'center'
          }, {
            field: 'mode',
            title: ENERGY.DEVICETYPECODE,
            align: 'center'
          }, {
            field: 'typeName',
            title: PAGE_TITLE.DEVICE_TYPE,
            align: 'center'
          }, {
            field: 'groupName',
            title: ENERGY.GROUPNAME,
            align: 'center'
          }
        ],
        data: []
      },
      // 扩展字段
      extendPropertyValueVoList: [],
      confirmRules: {},
      // 图片上传
      relationshipId: '',
      dialogImageUrl: '',
      fileList: [],
      delFileList: [],
      dialogVisible: false,
      uploadConfig: {
        headers: {
          token: window.localStorage.getItem('token').replace(/"/g, '')
        },
        url: IP.PUBLICIP + 'fileResource/uploadOneFile.action',
        data: {
          fileResourceTypeEnum: FILE_TYPE.REPAIR_DESCRIPTION_IMAGE,
          relationshipId: ''
        }
      },
      form: {
        deviceCode: '',
        repairType: null,
        extend18: null,
        repairTaskType: null,
        warrantyDescription: '',
        extendPropertyValueVoList: []
      },
      formInline: {
        deviceName: '',
        deviceCode: ''
      },
      repairTaskTypeData: [],
      extend18Data: []
    };
  },
  methods: {
    resetSearch() {
      this.formInline.deviceName = '';
      this.formInline.deviceCode = '';
      this.getDeviceListByTree();
    },
    /**
     * @description: 初始化添加维修单界面
     * @return void
    */
    initPage() {
      this.getTreeBydeviceGroup();
      this.getDeviceListByTree();
    },
    async getRepairTaskTypeData() {
      try {
        const res = await basicInformationGetListByBasicType({
          basicInformationEnum: 'REPAIR_TASK_TYPE'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          for (var i = 0; i < res.rows.length; i++) {
            arr.push({
              label: res.rows[i].basicValue,
              value: res.rows[i].basicId
            });
          }
          this.repairTaskTypeData = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getExtend18Data() {
      try {
        const res = await basicInformationGetListByBasicType({
          basicInformationEnum: 'REPAIR_TASK_IS_DOWN'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          for (var i = 0; i < res.rows.length; i++) {
            arr.push({
              label: res.rows[i].basicValue,
              value: res.rows[i].basicId
            });
          }
          this.extend18Data = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 根据车间分类获取设备
     * @return void
    */
    getTreeBydeviceGroup() {
      deviceGroupGetTreeAction().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nodeData = res.rows;
          for (var i = 0; i < this.nodeData.length; i++) {
            if (this.nodeData[i].code) {
              this.selectData = this.nodeData[i];
              break;
            }
          }
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 根据设备分类获取设备
     * @return void
    */
    getTreeBydeviceWorkshopType() {
      deviceWorkshopTypeGetTreeAction().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nodeData = res.rows;
          for (var i = 0; i < this.nodeData.length; i++) {
            if (this.nodeData[i].code) {
              this.selectData = this.nodeData[i];
              break;
            }
          }
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 获取设备列表
     * @param {string} type: 类型(groupIdList,typeIdList)
     * @return void
    */
    getDeviceListByTree() {
      var params = {
        current: this.deviceListTable.page.currentPage,
        size: this.deviceListTable.page.pageSize
      };
      if (this.typeSelect === '1') {
        params.groupIdList = this.selectDeviceFilter;
      }
      if (this.typeSelect === '2') {
        params.typeIdList = this.selectDeviceFilter;
      }
      params.deviceName = this.formInline.deviceName;
      // params.deviceName = this.formInline.dev;
      deviceSelectDeviceByConditionAction(params).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.deviceListTable.data = res.rows;
          this.deviceListTable.page.total = res.total;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 切换tab页面
     * @param {string} tabid
     * @return void
    */
    switchTab(tabid) {
      this.typeSelect = tabid;
      if (tabid === '1') {
        this.getTreeBydeviceGroup();
      } else {
        this.getTreeBydeviceWorkshopType();
      }
    },
    /**
     * @description: 执行table分页
     * @param {string} currentPage 第几页
     * @param {string} pageSize 一页几条
     * @return: void
     */
    handleDeviceListTablePageChange(event) {
      this.deviceListTable.page.currentPage = event.currentPage;
      this.deviceListTable.page.pageSize = event.pageSize;
      this.getDeviceListByTree();
    },
    /**
     * @description: 选择table上的单选
     * @param {Object} row 数据
     * @return: void
     */
    radioChangeEvent(row) {
      // this.clearData();
      this.selectRow = row.row;
      this.form.deviceCode = this.selectRow.deviceCode;
      this.form.repairType = null;
      this.getRepairNameData();
    },
    /**
     * @description: 点击树结构元素
     * @param {object} data 树结构渲染数据
     * @param {object} treeNode 树结构真实数据
     * @return void
    */
    handleNodeClick(events, nodes, obj) {
      this.selectData = nodes.name;
      this.selectDeviceFilter = [];
      this.selectDeviceFilter = this.getChildren(this.selectDeviceFilter, nodes);
      this.deviceListTable.page.currentPage = 1;
      this.getDeviceListByTree();
      this.clearData();
    },
    /**
     * @description: 递归获取子级数据
     * @param row 行数据
     * @return: void
     */
    getChildren(ids, treeNode) {
      ids.push(treeNode.id);
      if (treeNode.isParent) {
        for (var obj in treeNode.children) {
          this.getChildren(ids, treeNode.children[obj]);
        }
      }
      return ids;
    },
    /**
     * @description: 检查数据是否填入
     * @return: void
    */
    checkDataFilled: function() {
      var fillinData = false;
      // for (var key in this.createData) {
      //   if (!this.createData[key]) {
      //     fillinData = false;
      //   }
      // }
      if (this.form.repairType) {
        fillinData = true;
      }
      return fillinData;
    },
    /**
     * @description: 处理点击取消
     * @return: void
    */
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
    */
    confirmAction() {
      // if (this.handleConfirm) {
      //   this.handleConfirm(this.createData);
      //   this.clearData();
      // }
      this.$refs.form.validate(valid => {
        if (valid) {
          // alert('111');
          // this.confirmAction();
          this.saveTask();
        }
      });
    },
    saveTask() {
      const resourceIdListStr = this.fileList.map(item => {
        var json = '';
        if (!item.flag) {
          json = item.response.rows.resourceId;
        } else {
          json = item.id;
        }
        return json;
      });

      var param = {
        deviceCode: this.form.deviceCode,
        repairTypeIdList: this.form.repairType,
        warrantyDescription: this.form.warrantyDescription
      };

      if (this.form.extend18) {
        param.extend18 = this.form.extend18;
      }
      if (this.form.repairTaskType) {
        param.repairTaskType = this.form.repairTaskType;
      }

      repairTaskinsertRepairTask(param)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // this.getRepairTypeList();
            // this.getFixTableData();
            if (resourceIdListStr.length > 0) {
              this.saveRepairImg(res.rows.repairTaskId, resourceIdListStr);
            }
            if (this.extendPropertyValueVoList.length > 0) {
              this.saveExtendValue(res.rows.repairTaskId, this.extendPropertyValueVoList);
            }
            if (this.delFileList.length !== 0) {
              this.delImg();
            }
            showSuccessMessage(res.message);
            setTimeout(() => {
              this.handleConfirm();
              this.clearData();
            }, 300);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    saveRepairImg(id, arr) {
      var jsonArr = [];
      for (var i = 0; i < arr.length; i++) {
        var json = {
          relationshipId: id,
          resourceId: arr[i]
        };
        jsonArr.push(json);
      }
      fileResourceinsertOrUpdateBatch({}, jsonArr)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 处理点击关闭
     * @return: void
    */
    closeAction: function() {
      if (this.handleClose) {
        this.clearData();
        this.handleClose();
      }
    },
    /**
     * @description: 检查该属性是否有值
     * @param: {Object} data 属性数据
     * @return: void
    */
    hasValue: function(data) {
      var flag = false;
      if (data && data !== '') {
        flag = true;
      }
      return flag;
    },
    /**
     * @description: 根据设备查询维修(故障)类型
     * @return: void
    */
    getRepairNameData: function() {
      this.createData.repairType = null;
      this.repairTypeloading = true;
      selectRepairTypeByDeviceCode({
        deviceCode: this.form.deviceCode
      })
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            var result = {
              name: 'repairTypeData',
              data: res.rows
            };
            this.repairTypeloading = false;
            this.$store.commit('setFiltersData', result);
          }
        });
    },
    /**
     * @description: 清除数据
     * @return: void
    */
    clearData() {
      this.selectRow = null;
      this.createData = {
        repairType: null,
        deviceCode: null
      };
      this.extendPropertyValueVoList = [];
      this.confirmRules = {};

      this.fileList = [];
      this.delFileList = [];
      this.form = {
        deviceCode: '',
        repairType: null,
        warrantyDescription: '',
        extendPropertyValueVoList: []
      };
    },
    /**
     * @description: 图片控制
     * @param 无
     * @return:无
     * @author: ZJM
     */
    handleRemove(file, fileList) {
      if (!file.flag) {
        this.delFileList.push(file.response.rows.resourceId);
        this.fileList = fileList;
      } else {
        this.delFileList.push(file.id);
        this.fileList = fileList;
      }
    },
    updateSuccess(response, file, fileList) {
      this.fileList.push(file);
      if (response.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
        // this.fileList.push({
        //   id: response.rows.resourceId,
        //   url: IP.PUBLICIP + response.rows.fileUrl,
        //   relationshipId: response.rows.relationshipId
        // });
      }
    },
    /**
    * @description: 删除图片
    * @param {type}
    * @return:
    */
    async delImg() {
      try {
        const res = await delImgAction({ ids: this.delFileList }, []);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.delFileList = [];
          // 提示正常信息
          // showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    handlePictureCardPreview(file) {
      this.handleShowImg(file.url);
    },
    updateFiles(file) {
      var types = ['image/JPG', 'image/PNG', 'image/JPEG', 'image/jpeg', 'image/jpg', 'image/png'];
      const isImage = types.includes(file.type);
      if (!isImage) {
        this.$message.error('上传图片只能是 JPG、JPEG、PNG 格式!');
        return false;
      }
      const isLtSize = file.size / 1024 / 1024 < 5;
      if (!isLtSize) {
        this.$message.error('上传图片大小不能超过 5MB!');
        return false;
      }
    },
    async setTableColumn() {
      try {
        const res = await extendPropertyGetListAction(
          {},
          {
            conditionJson1: [
              {
                column: 'extendType',
                conditionJson1: [],
                conditionJson2: [],
                current: 0,
                operator: 1,
                operatorGroup: 0,
                orderBy: {},
                ptype: 'int',
                size: 0,
                type: 'eq',
                value: 6 // 维修任务的模板id
              }
            ]
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.extendPropertyValueVoList = res.rows;
          var rule = {};
          rule.extendPropertyValueVoList = res.rows;
          this.toExtendRulesAndShow(rule);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 保存扩展字段数据
     * @return: void
     */
    saveExtendValue(id, arr) {
      var arrJson = arr.map((item) => {
        var json = {};
        json.extendId = item.extendId;
        json.relationshipTypeId = id;
        json.propertyCode = item.propertyCode;
        if (item.extendValue === null) {
          json.extendValue = '';
        } else {
          json.extendValue = item.extendValue;
        }
        return json;
      });
      extendPropertyValueInsertOrUpdateBatchAction({}, arrJson)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // this.getFixTableData();
            // showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 表单中扩展字段处理
     * @return: void
     */
    toExtendRulesAndShow(data) {
      // 保存扩展字段数据
      var arr = data.extendPropertyValueVoList.map(item => {
        // item.relationshipTypeId = data.repairTaskId;
        item.extendValue = item.extendValue ? item.extendValue : null;
        return item;
      });
      this.extendPropertyValueVoList = arr;
      var rules = JSON.parse(JSON.stringify(this.confirmRules));
      for (var item in this.extendPropertyValueVoList) {
        // 若类型是日期
        if (this.extendPropertyValueVoList[item].propertyType === 3) {
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: ['blur', 'change'] }
          ];
        } else {
          // 若类型是字符或数字
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: 'blur' }
          ];
        }
      }
      rules.repairType = [
        [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }]
      ];
      this.form.extendPropertyValueVoList = this.extendPropertyValueVoList;
      this.confirmRules = rules;
    },
    datetimeChange(item, e) {
      var _this = this;
      item.extendValue = e;
      _this.$forceUpdate();
    },
    change(e) {
      this.$forceUpdate();
    }
  },
  watch: {
    showDialog(val, oldVal) {
      if (val) {
        this.initPage();
      }
    }
  },
  created() {
    this.clearData();
    this.initPage();
    this.setTableColumn();
    this.getRepairTaskTypeData();
    this.getExtend18Data();
  }
};
