import addRepairTaskData from '../assets/json/fixTask/addRepairTask.json';
import deleteRepairTaskData from '../assets/json/fixTask/deleteRepairTask.json';
import allRepairTypeData from '../assets/json/fixTask/selectRepairType.json';
import moment from 'moment';
const FixTaskService = {
  // 创建维修任务单
  addRepairTask: function(data) {
    var result = addRepairTaskData;
    return result;
  },
  // 批量删除维修任务
  deleteRepairTask: function(data) {
    var result = deleteRepairTaskData;
    return result;
  },
  // 获取所有的维修类型
  getAllRepairTypeData: function() {
    var result = allRepairTypeData;
    return result;
  },
  // process filters data
  generateFilters: function(data) {
    var filters = {};
    for (var key in data) {
      if (data[key]) {
        filters.hasFilter = true;
        filters[key] = data[key];
      }
    }
    return filters;
  },
  // 生成维修表数据
  generateFixTableData(data) {
    data.forEach(function(item) {
      if (!item.startTime || !item.endTime) {
        item.totalTime = 0;
      } else {
        var t1 = moment(item.startTime);
        var t2 = moment(item.endTime);
        item.totalTime = t2.diff(t1, 'seconds');
      }
      item.users = [];
      // item.startTime = moment(item.startTime).format('YYYY-MM-DD HH:mm:ss');
      // item.occurTime = moment(item.occurTime).format('YYYY-MM-DD HH:mm:ss');
      // item.endTime = moment(item.endTime).format('YYYY-MM-DD HH:mm:ss');
      if (item.repairUserIdList) {
        if (typeof item.repairUserIdList === 'string') {
          item.users.push(item.repairUserNameMap[item.repairUserIdList]);
        } else {
          item.repairUserIdList.forEach(function(userID) {
            item.users.push(item.repairUserNameMap[userID]);
          });
        }
      }
    });
    return data;
  },
  // 处理文件数据
  generateFixTaskFileData(data) {
    data.forEach(function(item) {
      item.name = item.fileName;
    });
    return data;
  },
  // 批量添加和修改维修类型
  generateBatchUpdateRepairTypeData(data) {
    var res = [];
    if (!data || data.length === 0) {
      return [];
    } else {
      data.forEach(function(item) {
        var resItem = {
          downTime: item.downTime ? parseInt(item.downTime) : null,
          repairName: item.repairName ? item.repairName : null,
          repairTypeId: item.repairTypeId ? item.repairTypeId : null,
          state: item.state ? item.state : null,
          typeIdList: item.typeIdList ? item.typeIdList : [],
          typeNameList: item.typeNameList ? item.typeNameList : []
        };
        res.push(resItem);
      });
    }
    return res;
  }
};

export { FixTaskService };
